ul{
  font-family: sans-serif;
  list-style-type: none;
  margin: 0;
  padding: 0;
  background-color: #2F3544;
  color: #EED7C5;
  width: 100%;
  overflow: hidden;
}

li{
  float: left;
}

li:not(:first-child){
  float: right;
}

li a{
  display: inline-block;
  text-decoration: none;
  text-align: center;
  cursor: pointer;
}

li:not(:first-child) a:hover{
  background-color: #191C24;
}

li:first-child a{
  padding: 10px 70px;
  font-size: 2rem;
}

li:not(:first-child) a{
  padding: 17px 50px;
  font-size: 1.2rem;
}

#cc{
  width: 100%;
  background-color: #2F3544;
  color: gray;
  font-size: 1.2rem;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100px;
}

#cc:hover{
  color:#EED7C5;
}

#dropdown_element a{
  padding: 12px 16px;
  display: block;
  text-align: left;
}

#dropdown_element a:hover{
  background-color: #FBF8EF;
}

.isDisplayNone{
  display: none;
}


.search_focus{
  border: 2px solid #52050A;
  opacity: 1;
}

.not_reverse{
  flex-direction: row;
  flex-wrap: wrap;
}

.reverse{
  flex-direction: row-reverse;
  flex-wrap: wrap-reverse;
  justify-content: flex-end;
}



.pj{
  border: 0.5px solid gray;
  background-color: #f5eeea;
  transition-duration: 1s;
}

.pj:hover{
  border: 0.5px solid #52050A;
  background-color: #2F3544;
  overflow-y:scroll;
}

.pj:hover .card-front{
  opacity: 0;
}

.card-back{
  position:absolute;
  opacity: 0;
  transition-duration: 1s;
  height: max-content;
}

.pj:hover .card-back{
  opacity: 1;
}

.github{
  top: 20px;
  left:35%;
  font-size:100px;
  cursor: pointer;
  color: black;
}

.github:hover{
  color: white;
  transform: rotate(360deg);
}

.langs{
  position: absolute;
  top:65%;
}

.lang{
  font-size: 70px;
  width: 70px;
  margin: 0 20px;
}

.lang:hover{
  color: #FBF8EF;
}

::-webkit-scrollbar {
  width: 0px;
}

